<template>
  <li class="stat_icon">
    <img class="icon" :src = p_img_url @click="on_click_stat" @mouseover="on_hover_stat">
    <p :class="points_class">{{p_total}}({{p_base}}) </p>
  </li>
</template>

<script>



export default {
  name: 'StatIcon',
  props: {
    p_id: String,
    p_base: Number,
    p_total: Number,
    p_img_url: URL
  },

  methods: {
    on_click_stat()
    {
      console.log("Click stat button")
      this.$emit("click_stat", this.p_id)
    },

    on_hover_stat()
    {
      this.$emit("hover_stat", this.p_id)
    }
  },

  computed: {
    points_class() {
      return this.p_base >= 60 ? "max" : "normal" 
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  p {
    margin-top: 0%;
  }

  li {
    list-style: none;
    align-self: center;
  }

  .max {
    color: #969696;
  }

  .normal {
    color: white;
  }

  .icon {
    border-style: solid;
    border-width: 2px;
    border-color: #969696;
  }

</style>
